@import "/static/scss/vars";

.plover-component-tips {
  background: $neutral-100;
  padding: $spacing-sm;
  display: flex;
  gap: $spacing-sm;

  .plover-component-tips__content > * {
    font-size: 14px;
    margin-top: 0;
    margin-bottom: 0;
  }

  .plover-component-tips__content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 6px;
  }

  .plover-component-tips__icon {
    flex-shrink: 0;
  }

  &:not(.next-no-margin) {
    margin-bottom: $spacing;
  }

  a {
    padding: 0 0.25em;
    text-decoration: underline;
    font-weight: 500;
  }

  ul, ol {
    list-style: initial;
    padding-left: 1.5em;
  }
}
