@import "/static/scss/vars";

.plover-component-popover__toggle {
  width: 100%;
  align-items: center;
  -webkit-appearance: none;
  background: none;
  border: 0;
  border-radius: 2px;
  box-sizing: border-box;
  color: $wp-foreground;
  cursor: pointer;
  display: inline-flex;
  font-family: inherit;
  font-size: 13px;
  font-weight: 400;
  height: 36px;
  margin: 0;
  padding: 6px 12px;
  text-decoration: none;
  transition: box-shadow .1s linear;

  &:focus:not(:disabled) {
    box-shadow: 0 0 0 var(--wp-admin-border-width-focus) $wp-primary;
    outline: 3px solid #0000;
  }

  &.is-open {
    background-color: #f0f0f0;
  }

  .plover-component-popover__toggle-svg-icon {
    line-height: 1;

    svg {
      width: 28px;
      height: 18px;
      color: currentColor;
    }
  }
}

.plover-component-popover__toggle-wrapper {
  width: 100%;
  display: block;
  box-sizing: border-box;
}
