@import "/static/scss/vars";

.plover-component-code-editor {
  .CodeMirror {
    border: 1px solid $border-color;
    height: var(--plover-component-code-editor__height, 300px);
  }

  .CodeMirror-scroll {
    overflow-x: auto !important;
  }

  .CodeMirror pre.CodeMirror-placeholder {
    color: #999;
  }
}