.plover-components-unit-slider-control {
  margin-bottom: 12px;

  legend {
    margin-bottom: 8px;
  }

  .plover-components-unit-slider-control__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    .plover-components-unit-slider-control__unit-control {
      width: calc((100% - 24px) / 2);
      margin-bottom: 0;
      margin-right: 16px;
      flex-shrink: 0;
    }

    .plover-components-unit-slider-control__range-control {
      flex: 1;
      //margin-right: 12px;

      > div {
        height: 40px;
        display: flex;
        align-items: center;
      }
    }
  }
}
