@import "/static/scss/vars";

@keyframes plover-progress-bar-animation {
  0% {
    left: -50%;
  }
  100% {
    left: 100%;
  }
}

.plover-component-progress-bar__track {
  position: relative;
  width: 100%;
  height: var(--plover-component-progress-bar-height, 2px);
  background: $neutral-200;
  overflow: hidden;

  .plover-component-progress-bar__indicator {
    position: absolute;
    left: 0;
    top: 0;
    width: var(--plover-component-progress-bar-indicator-width, 50%);
    height: 100%;
    background: $primary;
  }

  .plover-component-progress-bar__indicator:not(.has-value) {
    animation-duration: 1.5s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-name: plover-progress-bar-animation;
  }
}
